export default "import React from 'react';\r\nimport {Col, Form, Row} from 'react-bootstrap';\r\n\r\nconst Column = () => {\r\n  return (\r\n    <div>\r\n      <Form>\r\n        <Row>\r\n          <Col xs={7}>\r\n            <Form.Control placeholder='City' />\r\n          </Col>\r\n          <Col>\r\n            <Form.Control placeholder='State' />\r\n          </Col>\r\n          <Col>\r\n            <Form.Control placeholder='Zip' />\r\n          </Col>\r\n        </Row>\r\n      </Form>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default Column;\r\n";