export default "import React from 'react';\r\nimport {Form} from 'react-bootstrap';\r\n\r\nconst Switches = () => {\r\n  return (\r\n    <div>\r\n      <Form>\r\n        <Form.Check\r\n          type='switch'\r\n          id='custom-switch'\r\n          label='Check this switch'\r\n        />\r\n        <Form.Check\r\n          disabled\r\n          type='switch'\r\n          label='disabled switch'\r\n          id='disabled-custom-switch'\r\n        />\r\n      </Form>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default Switches;\r\n";