export default "import React from 'react';\r\nimport {Form} from 'react-bootstrap';\r\n\r\nconst Inline = () => {\r\n  return (\r\n    <div style={{width: '100%'}}>\r\n      <Form>\r\n        {['checkbox', 'radio'].map((type) => (\r\n          <div key={`inline-${type}`} className='mb-3'>\r\n            <Form.Check\r\n              inline\r\n              label='1'\r\n              name='group1'\r\n              type={type}\r\n              id={`inline-${type}-1`}\r\n            />\r\n            <Form.Check\r\n              inline\r\n              label='2'\r\n              name='group1'\r\n              type={type}\r\n              id={`inline-${type}-2`}\r\n            />\r\n            <Form.Check\r\n              inline\r\n              disabled\r\n              label='3 (disabled)'\r\n              type={type}\r\n              id={`inline-${type}-3`}\r\n            />\r\n          </div>\r\n        ))}\r\n      </Form>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default Inline;\r\n";