export default "import React from 'react';\r\nimport {Form} from 'react-bootstrap';\r\n\r\nconst ColorDemo = () => {\r\n  return (\r\n    <div style={{width: '100%'}}>\r\n      <>\r\n        <Form.Label htmlFor='exampleColorInput'>Color picker</Form.Label>\r\n        <Form.Control\r\n          type='color'\r\n          id='exampleColorInput'\r\n          defaultValue='#563d7c'\r\n          title='Choose your color'\r\n        />\r\n      </>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default ColorDemo;\r\n";