export default "import React from 'react';\r\nimport {Form} from 'react-bootstrap';\r\n\r\nconst Default = () => {\r\n  return (\r\n    <div style={{width: '100%'}}>\r\n      <Form>\r\n        {['checkbox', 'radio'].map((type) => (\r\n          <div key={`default-${type}`} className='mb-3'>\r\n            <Form.Check\r\n              type={type}\r\n              id={`default-${type}`}\r\n              label={`default ${type}`}\r\n            />\r\n\r\n            <Form.Check\r\n              disabled\r\n              type={type}\r\n              label={`disabled ${type}`}\r\n              id={`disabled-default-${type}`}\r\n            />\r\n          </div>\r\n        ))}\r\n      </Form>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default Default;\r\n";